html {
  box-sizing: border-box;
  //overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: $mobile-min-width;

  font-family: $alegreya;
  font-size: 30px;
  line-height: 1.5;
  color: $dark;

  background-color: #fff;

  @media (max-width: $mobile-max-width) {
  	font-size: 25px;
  }

  @media (max-width: $mobile-middle-width) {
  	font-size: 17px;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px;

  @media (max-width: $mobile-middle-width) {
    padding: 0 20px;
  }
}

.page-main {
  padding-bottom: 80px;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
