@font-face {
  font-family: "Abhaya Libre Regular";
  src:
    local("Abhaya Libre Regular"),
    local("Abhaya-Libre-Regular"),
    url("../fonts/abhayalibre-regular-webfont.woff2") format("woff2"),
    url("../fonts/abhayalibre-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Abhaya Libre Bold";
  src:
    local("Abhaya Libre Bold"),
    local("Abhaya-Libre-Bold"),
    url("../fonts/abhayalibre-bold-webfont.woff2") format("woff2"),
    url("../fonts/abhayalibre-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Afta Sans";
  src:
    local("BonvenoCF"),
    url("../fonts/aftasansthin-regular-webfont.woff2") format("woff2"),
    url("../fonts/aftasansthin-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
