@font-face {
  font-family: "Abhaya Libre Regular";
  src: local("Abhaya Libre Regular"), local("Abhaya-Libre-Regular"), url("../fonts/abhayalibre-regular-webfont.woff2") format("woff2"), url("../fonts/abhayalibre-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Abhaya Libre Bold";
  src: local("Abhaya Libre Bold"), local("Abhaya-Libre-Bold"), url("../fonts/abhayalibre-bold-webfont.woff2") format("woff2"), url("../fonts/abhayalibre-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Afta Sans";
  src: local("BonvenoCF"), url("../fonts/aftasansthin-regular-webfont.woff2") format("woff2"), url("../fonts/aftasansthin-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  font-family: "Abhaya Libre Regular", "Georgia", serif;
  font-size: 30px;
  line-height: 1.5;
  color: #252525;
  background-color: #fff;
}

@media (max-width: 767px) {
  body {
    font-size: 25px;
  }
}

@media (max-width: 480px) {
  body {
    font-size: 17px;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px;
}

@media (max-width: 480px) {
  .container {
    padding: 0 20px;
  }
}

.page-main {
  padding-bottom: 80px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.page-header {
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 10px solid #cdcecf;
  background-color: #2f98af;
}

@media (max-width: 480px) {
  .page-header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.page-header h1 {
  margin: 0;
  font-family: "Afta Sans", "Arial", "Helvetica", sans-serif;
  font-size: 60px;
  font-weight: normal;
  color: #fff;
  letter-spacing: 3px;
  text-transform: uppercase;
}

@media (max-width: 767px) {
  .page-header h1 {
    font-size: 40px;
  }
}

@media (max-width: 480px) {
  .page-header h1 {
    font-size: 30px;
  }
}

.content:first-of-type {
  padding-top: 40px;
}

@media (max-width: 480px) {
  .content:first-of-type {
    padding-top: 10px;
  }
}

.content h2 {
  margin-top: 80px;
  font-family: "Abhaya Libre Bold", "Georgia", serif;
  font-size: 30px;
}

@media (max-width: 767px) {
  .content h2 {
    font-size: 25px;
  }
}

@media (max-width: 480px) {
  .content h2 {
    margin-top: 40px;
    font-size: 17px;
  }
}

.content h2 span {
  font-family: "Abhaya Libre Regular", "Georgia", serif;
  font-weight: normal;
}

.content span.formula {
  position: relative;
}

.content span.small {
  display: inline-block;
  font-size: 14px;
}

.content span.small.bottom {
  padding-top: 10px !important;
}

.content span.small.top {
  margin-bottom: -3px;
}

.content span.big-bracket {
  font-size: 35px;
}

@media (max-width: 767px) {
  .content span.big-bracket {
    font-size: 30px;
  }
}

.content sup,
.content sub {
  font-size: 16px;
}

@media (max-width: 767px) {
  .content sup,
  .content sub {
    font-size: 14px;
  }
}

.content sup.formula {
  position: absolute;
  top: 5px;
  right: -7px;
}

.content sub.formula {
  position: absolute;
  right: -5px;
  bottom: 0;
}

.content figure {
  margin-right: 0;
  margin-left: 0;
}

.content .fraction {
  display: inline-block;
  padding: 0 5px;
  text-align: center;
  transform: translateY(25%);
}

.content .fraction .top, .content .fraction .bottom {
  padding: 0 5px;
}

.content .fraction .bottom {
  display: block;
  border-top: 1px solid #000;
}

.content .image-wrap-center {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 60px;
}

.content .custom-1 {
  margin-top: -40px;
}

@media (max-width: 480px) {
  .content .custom-1 {
    margin-top: -21px;
  }
}

.content .custom-2 {
  font-size: 20px;
}

@media (max-width: 480px) {
  .content .custom-2 {
    font-size: 14px;
  }
}
