.content {
  &:first-of-type {
    padding-top: 40px;

    @media (max-width: $mobile-middle-width) {
      padding-top: 10px;
    }
  }

  h2 {
    margin-top: 80px;
    font-family: $alegreya-bold;
    font-size: 30px;

    @media (max-width: $mobile-max-width) {
      font-size: 25px;
    }

    @media (max-width: $mobile-middle-width) {
      margin-top: 40px;
      font-size: 17px;
    }

    span {
      font-family: $alegreya;
      font-weight: normal;
    }
  }

  p {

  }

  span {
    &.formula {
      position: relative;
    }

    &.small {
      display: inline-block;
      font-size: 14px;

      &.bottom {
        padding-top: 10px !important;
      }

      &.top {
        margin-bottom: -3px;
      }
    }

    &.big-bracket {
      font-size: 35px;

      @media (max-width: $mobile-max-width) {
        font-size: 30px;
      }
    }
  }

  sup,
  sub {
    font-size: 16px;

    @media (max-width: $mobile-max-width) {
      font-size: 14px;
    }
  }

  sup {
    &.formula {
      position: absolute;
      top: 5px;
      right: -7px;
    }
  }

  sub {
    &.formula {
      position: absolute;
      right: -5px;
      bottom: 0;
    }
  }

  figure {
    margin-right: 0;
    margin-left: 0;
  }

  .fraction {
    display: inline-block;
    padding: 0 5px;
    text-align: center;
    transform: translateY(25%);

    .top, .bottom {
      padding: 0 5px;
    }

    .bottom {
      display: block;
      border-top: 1px solid #000;
    }
  }

  .image-wrap-center {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 60px;
  }

  .custom-1 {
    margin-top: -40px;

    @media (max-width: $mobile-middle-width) {
      margin-top: -21px;
    }
  }

  .custom-2 {
    font-size: 20px;

    @media (max-width: $mobile-middle-width) {
    	font-size: 14px;
    }
  }
}
