.page-header {
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 10px solid #cdcecf;
  background-color: #2f98af;

  @media (max-width: $mobile-middle-width) {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  h1 {
    margin: 0;
    font-family: $afta;
    font-size: 60px;
    font-weight: normal;
    color: #fff;
    letter-spacing: 3px;
    text-transform: uppercase;

    @media (max-width: $mobile-max-width) {
      font-size: 40px;
    }

    @media (max-width: $mobile-middle-width) {
      font-size: 30px;
    }
  }
}
